import React, { useState } from 'react';

import { Toast } from 'native-base';

import {
  Box,
  Button,
  Center,
  Modal,
  Stack,
  Typography,
} from '@onekeyhq/components';

const Modal1 = () => {
  const [visible, setVisible] = useState(false);
  return (
    <Box>
      <Button type="primary" onPress={() => setVisible(!visible)}>
         1
      </Button>
      <Modal visible={visible} onClose={() => setVisible(!visible)}>
        <Center flex="1">
          <Typography.Body2></Typography.Body2>
        </Center>
      </Modal>
    </Box>
  );
};

const Modal2 = () => (
  <Modal trigger={<Button> 2 trigger </Button>}>
    <Center flex="1">
      <Typography.Body2></Typography.Body2>
    </Center>
  </Modal>
);

const Modal3 = () => (
  <Modal
    trigger={<Button> 3</Button>}
    footer={
      <Center height="24px">
        <Typography.Body2> footer</Typography.Body2>
      </Center>
    }
  >
    <Center flex="1">
      <Typography.Body2></Typography.Body2>
    </Center>
  </Modal>
);

const Modal4 = () => (
  <Modal
    header="MODAL 4"
    trigger={<Button> 4</Button>}
    hideSecondaryAction
    onPrimaryActionPress={({ onClose }) => {
      onClose?.();
      Toast.show({
        title: ' primary action',
      });
    }}
  >
    <Center flex="1">
      <Typography.Body2></Typography.Body2>
    </Center>
  </Modal>
);

const Modal5 = () => {
  const [visible, setVisible] = useState(false);

  return (
    <>
      <Button onPress={() => setVisible(!visible)}> 5</Button>
      <Modal
        hideSecondaryAction
        closeable={false}
        header="MODAL 5"
        visible={visible}
        onPrimaryActionPress={({ onClose }) => {
          onClose?.();
          Toast.show({
            title: ' primary action',
          });
        }}
      >
        <Center>
          <Typography.Body2 my="2">
            
          </Typography.Body2>
          <Button onPress={() => setVisible(!visible)}> 5</Button>
        </Center>
      </Modal>
    </>
  );
};

const Modal6 = () => (
  <>
    <Modal
      hideSecondaryAction
      header="MODAL 6"
      onClose={() => Math.random() > 0.5}
      trigger={<Button> 6</Button>}
    >
      <Center>
        <Typography.Body2 my="2">
           onClose 
        </Typography.Body2>
      </Center>
    </Modal>
  </>
);

const ModalGallery = () => (
  <Center flex="1" bg="background-hovered">
    <Stack
      direction={{
        base: 'column',
        md: 'row',
      }}
      space={2}
    >
      <Modal1 />
      <Modal2 />
      <Modal3 />
      <Modal4 />
      <Modal5 />
      <Modal6 />
    </Stack>
  </Center>
);

export default ModalGallery;
