/* eslint-disable @typescript-eslint/no-unsafe-member-access */
/* eslint-disable @typescript-eslint/no-unsafe-assignment */
import { action, makeObservable, observable } from 'mobx';
import { Buffer } from 'buffer';

type Listener = (state: BleExchange) => boolean;

class BleExchange {
  debug = false;

  requestNeeded = false;

  activitySession = undefined;

  isReadDone = false;

  buffer = [];

  headbuffer = [];

  maxSize = 0;

  constructor() {
    makeObservable(this, {
      isReadDone: observable,
      buffer: observable,
      activitySession: observable,
      acquire: action.bound,
      addBuffer: action,
      release: action.bound,
    });
  }

  acquire(): void {
    this.clearData();
    this.requestNeeded = true;
    this.activitySession = this.createSession();
  }

  addBuffer(input: Buffer): void {
    // TODO: MTU
    // 1. 9header(?## + 2 + 4) + payload 2.
    // (header)
    //  "?"
    const data = Buffer.from(input);
    if (
      data.length > 9 &&
      data[0] === '?'.charCodeAt(0) &&
      data[1] === '#'.charCodeAt(0) &&
      data[2] === '#'.charCodeAt(0)
    ) {
      this.headbuffer = [...data.subarray(1, 9)];
      this.maxSize = data.readIntBE(5, 4);
      this.buffer = [...data.subarray(9)];
      if (this.debug) {
        console.log('BleExchange', '', '', this.maxSize);
      }
    } else {
      if (this.debug) {
        console.log('BleExchange', '', '', data.length);
      }
      this.buffer = this.buffer.concat([...data]);
    }
    if (this.debug) {
      console.log('BleExchange', '', this.buffer.length);
    }
    if (this.buffer.length >= this.maxSize) {
      if (this.debug) {
        console.log(
          'BleExchange',
          '',
          '',
          this.buffer.length,
        );
      }
      this.isReadDone = true;
    }
  }

  clearData(): void {
    this.requestNeeded = false;
    this.isReadDone = false;
    this.buffer = [];
    this.headbuffer = [];
  }

  release(): void {
    this.clearData();
    this.activitySession = undefined;
  }

  getData(): Buffer {
    return Buffer.from(this.headbuffer.concat(this.buffer));
  }

  createSession = (): string => {
    let guid = '';
    for (let i = 1; i <= 32; i += 1) {
      const n = Math.floor(Math.random() * 16.0).toString(16);
      guid += n;
      if (i === 8 || i === 12 || i === 16 || i === 20) guid += '-';
    }
    return guid;
  };

  async waitUtil(listener: Listener, timeout = 0.5) {
    return new Promise((resolve) => {
      const id = setTimeout(() => {
        const status = listener(this);
        // if (status) {
        //   clearInterval(id);
        //   console.log('--resolve', this.isReadDone);
        return resolve(this);
        // }
      }, timeout * 1000);
    });
  }
}

export default new BleExchange();
